export interface ICreateAttachedFile {
  file_name: string;
  file_path: string;
}

export interface ICreateProjectCommonReportRequest {
  title?: string;
  report_text?: string;
  attached_files: ICreateAttachedFile[];
  project_id: string;
  report_date: string;
}
export type IUpdateProjectCommonReportRequest = Partial<
  Omit<ICreateProjectCommonReportRequest, 'project_id'>
>;

export interface ICreateProgressCheckReportRequest
  extends ICreateProjectCommonReportRequest {
  execution_budget_unit_id: string;
  progress_report_data: object;
  total_performance_percentage?: string;
  consumed_workers_percentage?: string;
  reported_worker_ids: string[];
}
export type IUpdateProgressCheckReportRequest = Partial<
  Omit<ICreateProgressCheckReportRequest, 'project_id'>
>;

export interface ICreateProjectBudgetChangeRequest
  extends ICreateProjectCommonReportRequest {
  change_reason_code: string;
  billing_client_id: string;
}
export type IUpdateProjectBudgetChangeRequest = Partial<
  Omit<ICreateProjectBudgetChangeRequest, 'project_id'>
>;

export interface ICreateProjectCloseReportRequest
  extends ICreateProjectCommonReportRequest {
  close_report_checklist: object;
}
export type IUpdateProjectCloseReportRequest = Partial<
  Omit<ICreateProjectCloseReportRequest, 'project_id'>
>;

export type ICreateProjectMeetingReportRequest =
  ICreateProjectCommonReportRequest;
export type IUpdateProjectMeetingReportRequest = Partial<
  Omit<ICreateProjectMeetingReportRequest, 'project_id'>
>;

export interface ICreateProjectStatusUpdateReportRequest
  extends ICreateProjectCommonReportRequest {
  project_status_code: string;
}
export type IUpdateProjectStatusUpdateReportRequest = Partial<
  Omit<ICreateProjectStatusUpdateReportRequest, 'project_id'>
>;

export interface ICreateProjectWorkerReportRequest
  extends ICreateProjectCommonReportRequest {
  report_type_code?: string;
  reported_worker_ids: string[];
}
export type IUpdateProjectWorkerReportRequest = Partial<
  Omit<ICreateProjectWorkerReportRequest, 'project_id'>
>;

export interface IAttachedFileResponse {
  file_name: string;
  file_path: string;
}

export interface IProjectCommonReportResponse {
  id: string;
  title?: string;
  report_text?: string;
  attached_files: IAttachedFileResponse[];
  project_id: string;
  report_date: string;
  create_user_id: string;
}

export interface IProgressCheckReportResponse
  extends IProjectCommonReportResponse {
  execution_budget_unit_id: string;
  progress_report_data: object;
  total_performance_percentage: number;
  consumed_workers_percentage: number;
  reported_worker_ids: string[];
}

export interface IProjectBudgetChangeResponse
  extends IProjectCommonReportResponse {
  change_reason_code: string;
  billing_client_id: string;
}

export interface IProjectCloseReportResponse
  extends IProjectCommonReportResponse {
  close_report_checklist: object;
}

export type IProjectMeetingReportResponse = IProjectCommonReportResponse;

export interface IProjectStatusUpdateReportResponse
  extends IProjectCommonReportResponse {
  project_status_code: string;
}

export interface IProjectWorkerReportResponse
  extends IProjectCommonReportResponse {
  report_type_code?: string;
  reported_worker_ids: string[];
}
